.main_wrapper {
    width: 100%;
    display: flex;
    //
    &.outgoing {
        justify-content: flex-end;

        .content_wrapper {
            border-bottom-right-radius: 4px;
            &,
            &:before {
                background: rgb(44, 139, 0);
            }
            &:before {
                right: -5px;

                border-top-right-radius: 71px;
            }
        }
    }
    //
    &.incoming {
        justify-content: flex-start;

        .content_wrapper {
            background: rgb(170, 175, 168);
            border-bottom-left-radius: 4px;
            &,
            &:before {
                background: rgb(170, 175, 168);
            }
            &:before {
                left: -5px;

                border-top-left-radius: 71px;
            }
        }
    }
    //
    .content_wrapper {
        box-sizing: border-box;
        padding: 19px;
        padding-bottom: 17px;
        margin: 5px;
        border-radius: 20px;
        max-width: 80%;
        position: relative;

        &:before {
            content: "";
            display: block;
            position: absolute;
            height: 10px;
            width: 10px;
            bottom: 0;
        }

        .content {
            width: 100%;
            .username {
                color: rgb(0, 0, 195);
                font-weight: 600;
            }
            .message_text {
                font-size: 16px;

                word-break: break-word;
                white-space: pre-wrap;
                display: flow-root;
                unicode-bidi: plaintext;
            }
            .message_info {
                float: right;
                display: flex;
                align-items: center;
                margin: 7px;
                margin-bottom: 0;
                margin-right: 0;
                .time {
                    font-weight: 550;
                    color: rgba(0, 0, 0, 0.808);
                    margin-bottom: 0;
                    margin-right: 0;
                }
                .delivery_info {
                    margin-left: 4.5px;
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
}
