$min-height: 40px;
.formWrapper {
    width: 100%;
    background: rgb(221, 221, 221);
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    z-index: 50;

    min-height: $min-height;

    box-sizing: border-box;
    padding: 5px 10px;

    .smiles_button_wrapper {
        background: red;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        flex-shrink: 0;
        align-items: center;
    }

    .message_textarea_wrapper {
        min-height: $min-height;

        display: flex;
        align-items: center;

        flex-grow: 1;

        .message_input {
            background: transparent;
            resize: none;
            outline: none;
            border: none;
            height: 100%;
            width: 100%;
            overflow: hidden;
            overflow-y: scroll;
            box-sizing: border-box;
            padding: 10px 0;
            padding-left: 20px;
            font-size: 16px;

            max-height: 160px !important;

            &::-webkit-scrollbar {
                width: 0;
            }
        }
    }

    .button,
    .openSendFile {
        height: $min-height;
        width: $min-height;
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }

    .button {
        outline: none;
        border: none;
        background: blue;
    }
    .openSendFile {
        background: brown;
    }
}
