@import "../../styles/vars/sizes";

$chat-max-width: 700px;

.main_wrapper {
    background: #000;
    // height: 100vh;
    box-sizing: border-box;
    // padding-bottom: 61px;

    background: rgb(200, 200, 200);

    position: fixed;
    top: $nav_height;
    bottom: 0;
    left: 0;
    right: 0;

    .main_content_wrapper {
        height: 100%;
        position: relative;
        margin: 0 auto;

        .main_content {
            height: 100%;
            box-sizing: border-box;

            // position: relative;

            .main {
                height: 100%;

                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;

                .top_wrapper {
                    width: 100%;
                    background: rgb(155, 0, 116);
                    box-sizing: border-box;

                    max-width: $chat-max-width;

                    .top_wrapper_content {
                        padding: 5px 15px;
                    }
                    .notConncetedMessageWrapper {
                        box-sizing: border-box;
                        padding: 0 20px;
                        background: red;
                        border-bottom: 1px solid rgb(153, 0, 0);
                        .notConncetedMessageContent {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            .notConncetedMessageText {
                                font-size: 18px;
                                &:after {
                                    content: "";

                                    animation-name: text_dots_animation;
                                    animation-iteration-count: infinite;
                                    animation-duration: 1s;

                                    @keyframes text_dots_animation {
                                        0% {
                                            content: "";
                                        }
                                        25% {
                                            content: ".";
                                        }
                                        50% {
                                            content: "..";
                                        }
                                        70% {
                                            content: "...";
                                        }
                                    }
                                }
                            }
                            .notConncetedMessageSpinnerWrapper {
                                display: inline-flex;
                                justify-content: center;
                                align-items: center;
                                .notConncetedMessageSpinner {
                                    height: 10px;
                                    width: 10px;
                                    border-radius: 50%;
                                    border-width: 1px;
                                    border-style: solid;
                                    border-color: #000;
                                    border-top-color: red;

                                    animation-name: spinning;
                                    animation-iteration-count: infinite;
                                    animation-duration: 1s;
                                    animation-timing-function: linear;
                                    transform: rotate(0deg);

                                    @keyframes spinning {
                                        from {
                                            transform: rotate(0deg);
                                        }
                                        to {
                                            transform: rotate(360deg);
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .conncetedMessage {
                        display: block;
                        width: 100%;
                        height: 5px;
                        background: green;
                    }
                }
                .messages_wrapper {
                    width: 100%;
                    box-sizing: border-box;

                    overflow: hidden;
                    overflow-y: scroll;
                    flex-grow: 1;

                    padding-top: 8px;
                    position: relative;

                    .messages_content {
                        max-width: $chat-max-width;
                        margin: 0 auto;
                        box-sizing: border-box;
                        height: 100%;

                        .messages_date_group {
                            .sticky_date_wrapper {
                                position: sticky;
                                top: 0;
                                width: 100%;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                margin: 4px auto;
                                z-index: 21;
                                .sticky_date {
                                    color: white;
                                    background: rgb(0, 37, 139);
                                    box-sizing: border-box;
                                    padding: 8px 14px;
                                    border-radius: 20px;
                                    font-size: 15.2px;
                                    opacity: 0.9;
                                }
                            }
                        }
                        .no_messages_yet_text_wrapper {
                            width: 100%;
                            display: flex;
                            justify-content: center;
                            .no_messages_yet_text {
                                color: rgb(22, 0, 56);
                                font-size: 20px;
                            }
                        }
                    }
                }
                .messageFormWrapper {
                    width: 100%;
                    max-width: $chat-max-width;
                }
            }
            //
        }
    }
}

.scrollDownBtn {
    $scrollDownSize: 55px;
    width: $scrollDownSize;
    height: $scrollDownSize;

    // background: rgb(70, 155, 0);

    position: fixed;
    right: 30px;
    bottom: 40px;
    transform: translateY(-100%);

    border-radius: 50%;
    overflow: hidden;
    justify-content: center;
    align-items: center;

    display: none;
    opacity: 0;

    transition: all 2s;

    &:hover {
        // background: #000;
        // TODO animation
    }
    &:active {
        // background: red;
    }
    &.scrollDownBtnVisible {
        display: flex;
        opacity: 1;
        // background: rgb(31, 31, 0);

        // visibility: visible;
    }

    &.scrollDownBtnEnterActive {
        background: red;
    }
    &.scrollDownBtnEnterDone {
        background: yellow;
    }
    &.scrollDownBtnExitActive {
        background: green;
    }
    &.scrollDownBtnExitDone {
        background: #000;
    }
}
.lastChild {
    display: inline-block;
    height: 10px;
    width: 100%;
    background: red;
}
